const initialState = {
	list : []
}

const tagsReducer = (state = initialState, action) => {
	switch(action.type) {
	case 'REMOVE_TAG':
		var updatedState = state
		updatedState.list.splice(action.target, 1)
		return Object.assign({}, updatedState)

	default:
		return state
	}
}

export default tagsReducer



